import {Component, ElementRef, OnInit, ViewChild} from '@angular/core';
import data from '../../../data/bannerthree.json';
import author from '../../../data/volunteer/volunteer.json';

@Component({
    selector: 'app-banner',
    templateUrl: './banner.component.html',
    styleUrls: ['./banner.component.scss'],
})
export class BannerComponent implements OnInit {
    public banner = data;

    public listBanner = [
        {
            titulo: 'Retiro de líderes',
            informacao: [
                {dia: null, data: '29/04/2022', horario: '19h'},
            ],
            local: 'Koinonia',
            link: '/old/public/retiro',
        },
        {
            titulo: 'Curso Acerte',
            informacao: [
                {dia: null, data: '10/04/2022'},
            ],
            local: null,
            link: '/old/public/acerte',
        },
    ];

    @ViewChild('myVideo') public video: ElementRef | undefined;


    // Author
    public getAuthor(items: string | any[]) {
        var elems = author.filter((item: { id: string; }) => {
            return items.includes(item.id);
        });
        return elems;
    }

    constructor() {
    }

    settings = {
        slidesToShow: 1,
        slidesToScroll: 1,
        arrows: true,
        dots: false,
        autoplay: false,
        responsive: [
            {
                breakpoint: 991,
                settings: {
                    arrows: false
                }
            }
        ]
    }

    ngOnInit(): void {
        this.toggleMute();
    }

    private toggleMute(): void {
        this.video?.nativeElement.play();
    }


}
